.Postea {
    width: 100%;
    height: auto;
    border-radius: 20px;
    
}

p{
    text-align: center !important;
    font-weight: 500 !important;
    font-size: 18px;
    margin-bottom: 0px;
}

.Formulario{
    margin: 30px;
    padding: 10px;
    display: grid;
    grid-template-columns: .1fr .9fr .5fr;
    grid-template-rows: 3rem 2rem 18rem 4rem 2rem;
    grid-template-areas: "l-autor autor autor"
                         "titulo titulo titulo"
                         "post post post"
                         "datetime datetime datetime"
                         "submit submit submit";
    column-gap: 10px;
    row-gap: 10px;
    border-radius: 20px;
    background: #e0e0e0;
    box-shadow:  8px 8px 16px #c3c3c3,
                 -8px -8px 16px #fdfdfd;
}

.author{
    grid-area: autor;
    align-self: center;
    color: white;
    margin: 10px;
    height: 25px;
    border-radius: 20px;
    background: #b0b0b0;
box-shadow: inset 5px 5px 10px #999999,
            inset -5px -5px 10px #c7c7c7;
}

#title{
    grid-area: titulo;
    align-self: center;
    margin-bottom: 25px !important;
    color: white;
    height: 25px;
    border-radius: 20px;
    background: #b0b0b0;
box-shadow: inset 5px 5px 10px #999999,
            inset -5px -5px 10px #c7c7c7;
}

#etiqueta-autor{
    grid-area: l-autor;
}

textarea:focus, select:focus, button:focus, submit:focus, input:focus{
    outline: none;
}

::placeholder{
    color: white;
}

#fecha-hora{
    grid-area: datetime;
    text-align: center;
}

#post-nuevo{
    grid-area: post;
    align-self: center;
    line-height: 30px;
    margin: 10px;
    height: 100%;
    border-radius: 20px;
    background: #b0b0b0;
box-shadow: inset 5px 5px 10px #999999,
            inset -5px -5px 10px #c7c7c7;
    text-align: center;
}

.btn-publicar{
    grid-area: submit;
    width: 80%;
    height: 30px;
    margin: auto;
    border-radius: 15px;
    align-self: center;
background: linear-gradient(145deg, #bcbcbc, #9e9e9e);
box-shadow:  8px 8px 10px #999999,
             -8px -8px 10px #c7c7c7;
    color: white;
}