body{
  background: #e0e0e0;
}
.App{
  display: grid;
  grid-template-columns: 1fr 3.5fr;
  grid-template-rows: .3fr .4fr .3fr;
  grid-template-areas: "columna-publica slider"
                       "entradas entradas"
                       "entradas entradas";
}
.slider{
  position: relative;
  height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #e0e0e0;
}

.image{
  height: auto; 
    width: auto; 
    max-width: 1000px; 
    max-height: 600px;
  border-radius: 10px;
  background: #e0e0e0;
box-shadow:  14px 14px 24px #c3c3c3,
             -14px -14px 24px #fdfdfd;
}

.right-arrow{
  position: absolute;
  top: 50%;
  right: 32px;
  font-size: 3rem;
  color: #000;
  z-index: 10;
  cursor: pointer;
  user-select: none;
}

.left-arrow{
  position: absolute;
  top: 50%;
  left: 32px;
  font-size: 3rem;
  color: #000;
  z-index: 10;
  cursor: pointer;
  user-select: none;
}

.slide{
  opacity: 0;
  transition-duration: 1s ease;
}

.slide.active {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.08);
}



h1{
  text-align: center;
  text-shadow: 14px 14px 24px #c3c3c3,
  -14px -14px 24px #fdfdfd;
}

.fa-react{
  color: #61dafb;
}

.fa-atom{
  color: #282c34;
}

.fa-walking{
  color: #61dafb;
}

.fa-blogger{
  color: #282c34;
}

.entradas{
  grid-area: entradas;
  width: 100%;
}