#post {
    align-self: justify;
    line-height: 30px;
    padding: 20px;
    margin: 10px;
    height: 100%;
    border-radius: 20px;
    background: #b0b0b0;
box-shadow: inset 5px 5px 10px #999999,
            inset -5px -5px 10px #c7c7c7;
    text-align: center;
}
#post h2 {
    color: white;
}
#post p {
    text-align: justify !important;
}
